<template>
    <div>
        <div class="columns ">
            <div class="column is-4 p-3">
                <div class="content" style="text-align: center">
                    <div class="tile is-ancestor p-20">
                        <div class="tile is-vertical is-parent">
                            <month-picker
                                    @change="showDate"
                                    @change-year="(v) => year = v"
                                    @clear="showClearText"
                                    :lang="lang"
                                    :clearable="isClearable"
                                    :editable-year="isEditableYear"
                                    :variant="selectedVariant"
                                    :show-year="showYear"
                                    :default-month = "default_month"
                            >
                            </month-picker>
                        </div>
                    </div>
                    <div class="tile is-ancestor p-20">
                        <b-table
                                :data="isEmpty ? [] : monthInfoTotal"
                                :striped="isStriped"
                                :narrowed="isNarrowed"
                                :mobile-cards="hasMobileCards"
                        >
                            <template slot-scope="props">
                                <b-table-column field="Tg" :label="table[0].credit" width="40" numeric>
                                    {{ props.row.credit }}
                                </b-table-column>

                                <b-table-column field="Name" :label="table[0].debit">
                                    {{ props.row.debit }}
                                </b-table-column>

                                <b-table-column field="Name" :label="table[0].profit">
                                    <p v-bind:class="{ sign: sign === 'negative'}">{{ props.row.profit }}</p>
                                </b-table-column>
                            </template>

                            <template slot="empty">
                                <section class="section">
                                    <div class="content has-text-grey has-text-centered">
                                        <p>
                                            <b-icon
                                                    icon="emoticon-sad"
                                                    size="is-large">
                                            </b-icon>
                                        </p>
                                        <p>Nothing here.</p>
                                    </div>
                                </section>
                            </template>
                        </b-table>
                    </div>
                </div>
            </div>
            <div class="column is-1">
            </div>
            <div class="column is-6">

                <div class="tile is-ancestor ">
                    <div class="buttons pt-2">
                        <b-button @click="selectedBtn(index)"
                                  v-for="(btn,index) in btns"
                                  v-bind:key="index"
                                  :class="{selBtn: btn.id === selBtn}"
                                  type="is-primary"
                        >
                            {{btn.label}}
                        </b-button>
                    </div>
                </div>
                <div class="tile is-ancestor ">
                    <div class="tile is-vertical is-parent" v-if="visible === 'debit/credit'">
                      <b-table
                                :data="isEmpty ? [] : monthCreditDebit"
                                :columns="columns"
                                :striped="isStriped"
                                :narrowed="isNarrowed"
                                :mobile-cards="hasMobileCards"
                        >
                        </b-table>
                        <hr>
                        <p class="title">–ö–æ–º–º—É–Ω–∞–ª—å–Ω—ã–µ –ø–ª–∞—Ç–µ–∂–∏</p>
                        <b-table
                                :data="isEmpty ? [] : monthBill"
                                :columns="columnsBill"
                                :striped="isStriped"
                                :narrowed="isNarrowed"
                                :mobile-cards="hasMobileCards"
                        >
                        </b-table>
                    </div>

                    <div class="tile is-vertical is-parent" v-if="visible === 'decommissioned'">
                        <b-table
                                :data="isEmpty ? [] : decommissioned_month"
                                :columns="columns_decom"
                                :striped="isStriped"
                                :narrowed="isNarrowed"
                                :mobile-cards="hasMobileCards"
                        >
                        </b-table>
                    </div>

                    <div class="tile is-vertical is-parent" v-if="visible === 'chart month'">
                            <chart :labels="labels" :datasets="datasets" :key="componentKey"/>
                    </div>

                    <div class="tile is-vertical is-parent" v-if="visible === 'chart year'">
                        <chart :labels="labelsYear" :datasets="datasetsYear" :key="componentKey"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { MonthPicker } from 'vue-month-picker'
    import {mapGetters} from "vuex";
    import chart from "./test";
    import moment from 'moment';


    export default {

        data() {
            return {
                btns: [
                    {id: 0, name: "debit/credit",  label: this.$t("report.buttons.debit/credit")},
                    // {id: 0, name: ""},
                    {id: 1, name: "decommissioned", label: this.$t("report.buttons.decommissioned")},
                    {id: 2, name: "chart month", label: this.$t("report.buttons.chartMonth")},
                    {id: 3, name: "chart year", label: this.$t("report.buttons.chartYear")},
                ],
                table: [{
                        credit: this.$t("report.credit"),
                        debit: this.$t("report.debit"),
                        profit: this.$t("report.profit"),
                 }],
                selBtn: 0,
                visible: "debit/credit",
                default_month: new Date().getMonth() +1,
                lang:  this.$i18n.locale,

                //------table--------
                hasMobileCards: true,
                isEmpty: false,
                narrowed: true,
                isStriped: false,
                isNarrowed: false,
                columnsBill: [
                    {
                        field: 'Date',
                        label: this.$t("report.table.date"),
                        centered: true,
                    },
                    {
                        field: 'Credit',
                        label: this.$t("report.table.bill"),
                        centered: true,
                    },
                ],
                columns: [
                    {
                        field: 'UnicID',
                        label: "ID",
                        centered: true,
                    },
                    {
                        field: 'Comment',
                        label: this.$t("warehouse.table.comment"),
                        centered: true
                    },
                    {
                        field: 'Credit',
                        label: this.$t("report.credit"),
                        searchable: true,
                        centered: true,
                    },
                    {
                        field: 'Debit',
                        label: this.$t("report.debit"),
                        searchable: true,
                        centered: true,
                    },
                    {
                        field: 'Date',
                        label: this.$t("warehouse.table.dateCreate"),
                        searchable: true,
                        centered: true,
                    },

                ],

                columns_decom: [
                    {
                        field: 'UnicID',
                        label: "ID",
                        centered: true,
                    },
                    {
                        field: 'ProductName',
                        label: this.$t("products.table.productName"),
                        searchable: true,
                        centered: true,
                    },
                    {
                        field: 'AmountDecom',
                        label: this.$t("products.table.amount"),
                        centered: true
                    },

                    {
                        field: 'Comment',
                        label: this.$t("warehouse.table.comment"),
                        searchable: true,
                        centered: true,
                    },
                    {
                        field: 'DateCreate',
                        label: this.$t("warehouse.table.dateCreate"),
                        searchable: true,
                        centered: true,
                    },

                ],
                debit: null,

                year: 0,
                clearEmittedText: null,
                isMonthPickerVisible: false,
                isClearable: true,
                isEditableYear: false,
                showYear: true,
                selectedDate: null,
                variants: ['default', 'dark'],
                selectedVariant: 'default',
                date: {
                    from: null,
                    to: null,
                    month: null,
                    year: null
                },
                labels:[],
                labelsYear:[
                    'January',
                    'February',
                    'March',
                    'April',
                    'May',
                    'June',
                    'July',
                    'August',
                    'September',
                    'October',
                    'November',
                    'December'
                ],
                datasets:[
                    {data: []},//credit
                    {data: []}
                ],
                datasetsYear:[
                    {data: []},//credit
                    {data: []},
                ],
                componentKey: 0,
                currentYear: '',
                sign: '',
            }
        },
        components: {
            MonthPicker,
            chart
        },
        computed: {
            ...mapGetters("credit", [
                'monthInfoTotal', 'monthCredit', 'creditYear', 'monthCreditDebit'
            ]),
            ...mapGetters("debit", [
                'monthDebit', 'debitYear'
            ]),
            ...mapGetters("supply", [
                'decommissioned_month'
            ]),
            ...mapGetters("utility_bills", [
                'monthBill', 'monthBillAmount'
            ]),
        },
        watch:{
            currentYear(){
                //building a new chart when the year is changed
                this.datasetsYear[0].data = [];
                this.getLastFirstInMonth();
           },

            //TODO if debit the same in 2 periods - sign class will not change
            monthInfoTotal(val){
                if(val.length){
                    let result = Math.sign(val[0].debit - val[0].credit);
                    console.log(val);
                    if(result === -1){
                        this.sign = 'negative';
                        console.log('Negative')
                    }else{
                        this.sign = 'positive';
                        console.log('Positive')
                    }
                }else {
                    this.sign = 'positive';
                }
            }
        },
        mounted() {
            this.showDate(this.date);
        },
        methods: {
            //datePicker
            showClearText () {
                this.clearEmittedText = 'emitted';
                window.setTimeout(() => {
                    this.clearEmittedText = null
                }, 1000)
            },

            //generating an array of dates [Sun Dec 01 2019 00:00:00 GMT+0200 (Eastern European Standard Time),...]
            getDateArray(start, end){
                let  arr = [];
                let dt = new Date(start);

                while (dt < end) {
                    arr.push(new Date(dt));
                    dt.setDate(dt.getDate() + 1);
                }
                return arr;
            },
            arrayOfDays(year, monthIndex){
                let days = this.daysInMonth(year, monthIndex); //31
                let array = [];
                for (let i = 0; i < days; i++) {
                    array.push(i+1);
                }
                return array;
            },
            showDate (date) {
                this.date = date;
                this.$store.dispatch('utility_bills/getMonthBill', date);
                this.$store.dispatch('credit/getMonthCredit', date);

                this.$store.dispatch('debit/getMonthDebit', date);
                this.$store.dispatch('supply/decommissionedMonthGet', date);

                //to trigger watcher currentYear only when year is really changed
                if(this.currentYear !== date.year){
                    this.currentYear = date.year;
                }
                //clean chart before render
                this.datasets[0].data = [];
                this.datasets[1].data = [];
                //[1,2,3,...]
                let arrayOfDaysX = this.arrayOfDays(date.year, date.monthIndex);
               //[Sun Dec 01 2019 00:00:00 GMT+0200 (Eastern European Standard Time),...]
                let dateArray = this.getDateArray(date.from, date.to);
                this.labels = arrayOfDaysX;

                // this.forceRerender();
                setTimeout(()=>{
                    //Y coordinat in "Month credit" - datasets[0].data : [100,12,15,190...]
                    this.creatingArray(dateArray, this.monthCredit);
                    //Y coordinat in "Month debit" - datasets[1].data : [100,12,15,190...]
                    this.creatingArray(dateArray, this.monthDebit);
                    // this.arrayOfCredits(dateArray)
                    // this.arrayOfDebits(dateArray)
                },300);


            },
            formatDateArray(array){
                //get all dates that have debit in format DD/MM/YYYY
                let dates = [];
                for (let i = 0; i < array.length; i++) {
                    let date = moment(array[i].Date).format('DD/MM/YYYY');
                    dates.push(date);
                }
                return dates;
            },
            formatDate(date){
                return moment(date).format('DD/MM/YYYY')
            },

            creatingArray(dateArray, arrayOfItems){
                //get all dates that have debit in format DD/MM/YYYY
                let dates = this.formatDateArray(arrayOfItems);

                //get unique dates from array
                let unique_array = [...new Set(dates)];//select only unique records
                let final_array = [];//[ 0: {date: Sun Dec 01 2019 00:00:00 GMT+0200 (Eastern European Standard Time), total: 0},...]

                for (let i = 0; i < dateArray.length; i++) {
                    for(let y = 0; y < unique_array.length; y++){
                        if(this.formatDate(dateArray[i]) == unique_array[y]){
                            let total = null;
                            for (let z = 0; z < arrayOfItems.length; z++) {
                                if(unique_array[y] == this.formatDate(arrayOfItems[z].Date)){
                                    //TODO Credit debit
                                    if(arrayOfItems[z].Debit){
                                        total += arrayOfItems[z].Debit;
                                    }else {
                                        total += arrayOfItems[z].Credit;
                                    }
                                }
                            }
                            final_array.push({ date: dateArray[i], total: total });
                        }
                    }
                    if(final_array.length > 0){
                        if(dateArray[i] !== final_array[final_array.length - 1].date){
                            final_array.push({ date: dateArray[i], total: 0 });
                        }
                    }else {
                        final_array.push({ date: dateArray[i], total: 0 });
                    }
                }
                let result = this.mapItems(final_array);
                if(arrayOfItems.length && arrayOfItems[0].Debit){
                    this.datasets[1].data = result;
                }else if(arrayOfItems.length && arrayOfItems[0].Credit){
                    this.datasets[0].data = result;
                }
                this.forceRerender();
            },
            mapItems(array){
                let result = array.map((item) => {
                    return  item.total;
                });
                return result;
            },
            toggle(data){
                this.visible = data;
            },
            selectedBtn(index){
                this.toggle(this.btns[index].name);
                this.selBtn = index;
                if(index === 3){
                    this.datasetsYear[0].data = this.creditYear;
                    this.datasetsYear[1].data = this.debitYear;
                    this.forceRerender();
                }
                if(index === 2){
                    this.forceRerender()
                }
            },
            daysInMonth(year, month){
                return new Date(year, month, 0).getDate()
            },
            forceRerender() {
                this.componentKey += 1;
            },
            getLastFirstInMonth(){
                let day = 1;
                let arrayStartEndMonths = [];
                for (let i = 1; i <= 12; i++) {
                let month = i;
                let date = this.currentYear+'-'+month+'-'+day;
                const startOfMonth = moment(date).startOf('month').format('YYYY-MM-DD hh:mm');
                const endOfMonth   = moment(date).endOf('month').format('YYYY-MM-DD hh:mm');
                arrayStartEndMonths.push({from: startOfMonth, to: endOfMonth});
                }
                this.$store.dispatch('credit/getYearCredit', arrayStartEndMonths).then(() =>  this.datasetsYear[0].data = this.creditYear);
                this.$store.dispatch('debit/getYearDebit', arrayStartEndMonths).then(() =>  this.datasetsYear[1].data = this.debitYear);
                this.forceRerender();
            }
        }
    }
</script>

<style>
    body {
        background-color: #FEFEFE;
    }
</style>

<style scoped>
    .sign {
        color: red;
    }
    @import url('https://fonts.googleapis.com/css?family=Oxygen&display=swap');
    .content {
        max-width: 800px;
        margin: 0 auto;
        font-family: 'Oxygen', sans-serif;
