<script>
    import Datepicker from 'vuejs-datepicker';
    import moment from "moment";
    import {mapGetters} from "vuex";
    import chart from "./test";
    // import { extendMoment } from 'moment-range';
    // const Moment = extendMoment(moment);

    export default {
        name: 'dayReport',
        components: {
            Datepicker,
            chart,
        },
        data() {
            return {
                date: '',
                labels: ["00:00", "01:00", "02:00", "03:00", "04:00", "05:00", "06:00", "07:00",
                         "08:00", "09:00", "10:00", "11:00", "12:00", "13:00", "14:00", "15:00", "16:00",
                         "17:00", "18:00", "19:00", "20:00", "21:00", "22:00", "23:00", "00:00"],
                datasets:[
                    {data: []},
                    {data: []}
                ],
                componentKey: 0,

                //--------buttons----------
                selBtn: 0,
                btns: [
                    {id: 0, name: "debit"},
                    {id: 1, name: "credit"},
                    {id: 2, name: "chart day"},
                    {id: 3, name: "decommissioned"},
                ],
                visible: "debit",

                //------table--------
                hasMobileCards: true,
                isEmpty: false,
                narrowed: true,
                isStriped: false,
                isNarrowed: false,
                columns: [
                    {
                        field: 'UnicID',
                        label: "ID",
                        centered: true,
                    },
                    {
                        field: 'Debit',
                        label: this.$t("report.debit"),
                        searchable: true,
                        centered: true,
                    },
                    {
                        field: 'Date',
                        label: this.$t("warehouse.table.dateCreate"),
                        searchable: true,
                        centered: true,
                    },

                    ]

            }
        },
        computed: {
            ...mapGetters("debit", [
                'dayDebit',
            ]),
        },
        mounted(){
           // this.getDebitHour()
        },
        filters: {
            moment: function (date) {
                return moment(date).format('HH:mm');
            }
        },
        watch: {
            date(val) {
                let date = moment(val).format('DD/MM/YYYY');
                let from = moment(date, 'DD/MM/YYYY', true).format();
                let to = moment(from).add('days', 1).format();
                let array = [{from: from, to: to}];
                this.getDayDebit(array)
            },
            dayDebit(dayTime){
                if(dayTime.length){
                let arrayTimeDebit = [];
                let arrayTime = [];
                for (let i = 0; i < dayTime.length; i++) {
                    let time = moment(moment.utc(dayTime[i].Date)).format('HH.mm.ss');
                    arrayTimeDebit.push({time: time, debit: dayTime[i].Debit})
                    arrayTime.push(time)
                }
                this.getDebitHour(arrayTime, arrayTimeDebit)
            }else {
                    console.log('no debit')
                }
            },

        },
        methods: {
            forceRerender() {
                this.componentKey += 1;
            },
            formArrayDebit(arrayTimes, arrayTimeDebit){
                let debit = 0;
                console.log("arrayTimes")
                console.log(arrayTimes)
                for(let j = 0; j < arrayTimes.length; j++){
                    for(let k = 0; k < arrayTimeDebit.length; k++){
                        if(arrayTimes[j] === arrayTimeDebit[k].time){
                            debit += arrayTimeDebit[k].debit;
                            console.log("arrayTimes[j]");
                            console.log(arrayTimes[j]);
                            console.log("arrayTimeDebit[k].debit");
                            console.log(arrayTimeDebit[k].debit)
                        }
                    }
                }
                if(debit > 0){
                    return debit;
                }else {
                    return 0;
                }
            },
            getDebitHour(arrayTime, arrayTimeDebit){
                console.log("arrayTimeDebit")
                console.log(arrayTimeDebit)
                let finalArrayTime = [];
                let minStart = "23:00";
                let min, max;
                for (let i = 0; i < 25; i++) {
                    if(i===0){
                        max = moment(minStart, "HH:mm").add(1, 'hours').format('HH:mm');
                        console.log("arrayTime");
                        console.log(arrayTime);
                        let result = arrayTime.filter(a => a > minStart && a < max);
                        let debit = this.formArrayDebit(result, arrayTimeDebit);
                        finalArrayTime.push(debit);
                        console.log(minStart +" - "+ max);
                        console.log("first "+debit);
                        console.log("result")
                        console.log(result)

                        // minStart = max;
                    }else {
                        // if(i === 2){
                        //     min = moment(minStart, "HH:mm").add(1, 'hours').format('HH:mm');
                        // }
                        min = moment(minStart, "HH:mm").add(1, 'hours').format('HH:mm');
                        max = moment(minStart, "HH:mm").add(2, 'hours').format('HH:mm');
                        console.log(min +" - "+ max);
                        let result = arrayTime.filter(a => a > min && a < max);
                        let debit = this.formArrayDebit(result, arrayTimeDebit);
                        console.log("second  "+debit);
                        finalArrayTime.push(debit);
                        minStart = min;
                    }
                }
                console.log("finalArrayTime");
                console.log(finalArrayTime);
                this.datasets[1].data = finalArrayTime;
                this.forceRerender();
            },
            getDayDebit(array){
                this.$store.dispatch('debit/getDayDebit', array);
            },
            selectedBtn(index){
                this.toggle(this.btns[index].name);
                this.selBtn = index;
                // if(index === 3){
                //     this.datasetsYear[0].data = this.creditYear;
                //     this.datasetsYear[1].data = this.debitYear;
                //     this.forceRerender();
                // }
                // if(index === 2){
                //     this.forceRerender()
                // }
            },
            toggle(data){
                this.visible = data;
            },
            // test(){
