            <div class="tile is-6 is-vertical is-parent">
                <div class="tile is-child is-2">
                </div>

                <!-------------------------------------------------------------------------------------------------------->
                <!--//********************************TABLE*******************************************************-->
                <!-------------------------------------------------------------------------------------------------------->
                <b-table
                        :data="isEmpty ? [] : bills"
