                        :mobile-cards="hasMobileCards">

                    <template slot-scope="props">

                        <b-table-column field="Date" label="ID">
                            {{ props.row.ID }}
                        </b-table-column>

                        <b-table-column field="Date" label="Date">
                            {{ props.row.DateCreate | formatDate }}
                        </b-table-column>

                        <b-table-column field="Date" label="Water">
                            {{ props.row.Water }}
                        </b-table-column>

                        <b-table-column field="Date" label="Gas">
                            {{ props.row.Gas }}
                        </b-table-column>

                        <b-table-column field="Date" label="Electricity">
                            {{ props.row.Electricity }}
                        </b-table-column>


<!--                        <b-table-column field="" label="   ">-->
<!--                            <b-button @click="updateNorm(props.row.Date)"-->
<!--                                      type="is-info"-->
<!--                                      v-show="visible !== props.row.Date"-->
<!--                                      :disabled="visible"-->
<!--                                      size="is-small"-->
<!--                                      expanded-->
<!--                            >-->
<!--                                Update-->
<!--                            </b-button>-->
<!--                            <b-button @click="saveNorm(props.row.Date)"-->
<!--                                      type="is-info"-->
