<!--                            </b-button>-->
<!--                        </b-table-column>-->

                        <b-table-column field="" label="  ">
                            <b-button @click="removeBill(props.row.ID)"
                                      type="is-light"
                                      :disabled="visible"
                                      v-show="visible !== props.row.Date"
                                      size="is-small"
