<script>
    import { mapGetters } from 'vuex';
    import { ValidationObserver } from 'vee-validate';
    import BInputWithValidation from '../components/inputs/BInputWithValidation';

    export default {
        name: 'norms',
        components: {
            ValidationObserver,
            BInputWithValidation,
        },
        data() {
            return {
                isEmpty: false,
                isStriped: false,
                isNarrowed: false,
                isHoverable: true,
                isLoading: false,
                hasMobileCards: true,
                narrowed: true,
                options: {
                    "resourceType": [
                        {text: 'gas', value: 1},
                        {text: 'metal', value: 2},
                        {text: 'oil', value: 3},
                        {text: 'send', value: 4},
                    ],
                },
                show: false,
                visible: false,
                newBill: {
                    'Tg': null,
                    'Water': null,
                    'Gas': null,
                    'Electricity': null,
                },
                objAmount: null,
                amount: null,
                objResource: null,
            }
        },
        mounted() {
            // this.getNorms();
            this.$store.dispatch('utility_bills/getBills');
        },
        computed: {
            ...mapGetters("utility_bills", [
                'bills',
            ]),
        },

        methods:{
            getNorms(){
                this.$store.dispatch('norms/getNorms', this.$route.params.tg)
            },
            removeBill(date){
                this.$store.dispatch('utility_bills/removeBill', date);
            },
            updateNorm(date){
                this.visible = date;
                console.log('update norm!')
            },
            updateAmount (e) {
                this.amount = e;
                this.objAmount = {'Amount': e, 'Date': this.visible};
                this.$store.commit('norms/UPDATE_AMOUNT', this.objAmount)
            },
            updateResource (e) {
                this.objResource = {'Resource': e, 'Date': this.visible};
                this.$store.commit('norms/UPDATE_RESOURCE', this.objResource)
            },
            saveNorm(date){
                this.$store.dispatch('norms/updateNorm', date);
                this.visible = false;
            },
            cancel(norm, index){
                this.visible = false;
                let obj = {'index': index, 'norm': norm};
                this.$store.dispatch('norms/getNorm', obj)
            },
            cancelAddNorm(){
                this.show = false;
                // this.$store.dispatch('norms/getNorm', this.$route.params.tg);
                this.reset();
            },
            reset(){
                this.newBill.Water = null;
                this.newBill.Electricity = null;
                this.newBill.Gas = null;
                requestAnimationFrame(() => {
                    this.$refs.observer.reset();
                });
            },
            callFunction: function () {
                var currentDateWithFormat = new Date().toJSON().slice(0, 10).replace(/-/g, '-');
                return currentDateWithFormat
            },
            async saveNewBill(){
                let copiedObject = Object.assign({}, this.newBill);
                await this.$store.dispatch('utility_bills/addBill', copiedObject).then(() =>  this.reset());
                // this.show = false;
            }
        }
    }
</script>

