import {ToastProgrammatic as Toast} from "buefy";
import Vue from "vue";
// import {i18n} from "../../plugins/i18n";

export default {
    namespaced: true,
    state: {
        bills: [],
        monthBill:[],
        monthBillAmount: null,
        monthBillAmountTest: 3,
    },
    getters: {
        bills: state => state.bills,
        monthBill: state => state.monthBill,
        monthBillAmount: state => state.monthBillAmount,
    },
    mutations: {
        SET_BILLS(state, bills){
            state.bills = bills;
        },
        ADD_BILL(state, bill){
            let id = state.bills[state.bills.length - 1].ID;
            console.log(id);
            Vue.set(bill, "ID", id+1);
            if(!state.bills){
                state.bills = [bill];
            }else {
                let b = state.bills.concat(bill);
                state.bills = b;
            }
        },
        REMOVE_BILL(state, id){
            let b = state.bills.filter(s => s.ID != id);
            state.bills = b;
            console.log("remove bill mutation!")
        },
        ADD_BILL_MONTH(state, data){
            console.log("data");
            console.log(data);
            if(data.length){
                console.log('yes');
                state.monthBill = data;
                state.monthBillAmount = Number(data[0].Credit);
            }else {
                state.monthBill = [];
                console.log('no')
            }
        }
    },
    actions: {
        async getBills({commit}){
            let response =  await window.axios.get('/utility_get');
            commit('SET_BILLS', response.data.Items);
        },
        async addBill({commit, state}, bill){
            let time = new Date();
            let id = state.bills[state.bills.length - 1].ID;
            let response = await window.axios.post('/utility_set', {
                Electricity: Number(bill.Electricity),
                Water: Number(bill.Water),
                Gas: Number(bill.Gas),
                DateCreate: time,
                Credit: Number(bill.Electricity) + Number(bill.Water)+Number(bill.Gas),
                Comment: "Communal bills",
                BillID: id+1,
            });
            if (response.status == 200 || response.status == 204) {
                // bill = bill.concat({DateCreate: time});
                Vue.set(bill, "DateCreate", time);
                commit('ADD_BILL', bill);

                Toast.open({
                    message: 'yes',
                    type: 'is-success'
                });
            }
        },
        async removeBill({commit}, id){
            let response =  await window.axios.post('/utility_remove', {
                ID: id,
            });
            if(response.status == 200 || response.status == 204){
                commit('REMOVE_BILL', id);
                console.log("yes")
            }
        },
        async getMonthBill({commit, state}, date){
            let response = await window.axios.post('/utility_bills', {
                From: date.from,
                To: date.to,
            });
            if (response.status == 200 || response.status == 204) {
                // let credit = 0;
                state.monthBillAmount = 0;
                if(response.data.Items === null) {
                    console.log("less then 0");
                    commit('ADD_BILL_MONTH', []);
                }else {
                    commit('ADD_BILL_MONTH', response.data.Items);
                }
            }
        },

    }
}
